<template>
  <div class="sort">
    <!-- 商品分类添加弹框 -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="标题" prop="title">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="图片">
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-change="changeImg"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="状态">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="add" v-if="info.isAdd">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { reg } from "../../../util/reg";
import {
  requestBannerAdd,
  requestBannerDetail,
  requestBannerUpdate,
} from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      list: "banner/list",
    }),
  },
  data() {
    return {
      imageUrl: "",
      form: {
        status: 1,
        title: "",
        img: null,
      },
      rules: {
        title: [
          { required: true, message: "请输入标题名称", trigger: "blur" },
          { min: 2, max: 5, message: "长度在 2 到 5 个字符", trigger: "blur" },
        ],
      },
    };
  },
  props: ["info"],
  mounted() {},
  methods: {
    //   vuex获取数据
    ...mapActions({
      requestList: "banner/requestList",
    }),

    // changeImg
    changeImg(e) {
      // 上传文件不能超过2M
      if (e.size > 2 * 1024 * 1024) {
        warningAlert("上传图片必须不能超过2M");
        return;
      }
      // 上传文件必须是图片
      var extname = e.name.slice(e.name.lastIndexOf("."));
      var extArr = [".png", ".jpg", ".gif", ".jpeg"];
      if (!extArr.some((i) => i === extname)) {
        warningAlert("上传文件必须是图片");
        return;
      }
      // 上传的文件
      var file = e.raw;
      this.imageUrl = URL.createObjectURL(file);
      this.form.img = file;
    },

    //重置内容
    empty() {
      (this.form = {
        status: 1,
        title: "",
        img: null,
      }),
        (this.imageUrl = "");
    },
    // 取消
    cancel() {
      this.$refs.form.resetFields();
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },
    // 添加
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          // 判断是否上传图片
          if (this.form.img === null && this.form.pid !== 0) {
            warningAlert("请上传图片");
            return;
          }
          requestBannerAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert("添加成功"); //添加成功弹框
              this.info.show = false;
              this.cancel();
              this.empty();
              //   再次请求数据
              this.requestList();
            }
          });
        } else {
          warningAlert("请将信息填写完整");
          return false;
        }
      });
    },

    // 获取某一条数据
    getDetail(id) {
      requestBannerDetail({ id: id }).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.imageUrl = this.$imgPre + res.data.list.img;
      });
    },

    // 修改
    update() {
      requestBannerUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   再次请求数据
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped lang='stylus'>
.sort >>> .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.sort >>> .el-upload:hover {
  border: 1px dashed #409EFF;
}

.sort >>> .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.sort >>> .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>


