<template>
  <div class>
    <!-- 管理员管理列表开始 -->
    {{this.list.attrs}}
    <el-table
      :data="list"
      style="width: 100%,margin-bottom:20px"
      row-key="id"
      border
      :tree-props="{children: 'children'}"
    >
      <el-table-column prop="id" label="规格编号" width="80"></el-table-column>
      <el-table-column prop="specsname" label="规格名称" width="180"></el-table-column>
      <el-table-column prop="rolename" label="规格属性">
        <template slot-scope="scope">
          <el-tag v-for="item in scope.row.attrs" :key="item">{{item}}</el-tag>
        </template>
      </el-table-column>

      <el-table-column label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status==1">启用</el-button>
          <el-button type="info" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 --> 
    <el-pagination
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="cPage"
      :page-size="size"
    ></el-pagination>
    <!-- 管理员管理列表结束 -->
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { requestNormsDelete } from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      list: "norms/list",
      total: "norms/total",
      size: "norms/size",
    }),
  },
  data() {
    return {};
  },
  props: [""],
  mounted() {
    this.requestTotal();
    this.requestList();
  },
  methods: {
    ...mapActions({
      requestList: "norms/requestList",
      requestTotal: "norms/requestTotal",
      changePage: "norms/changePage",
    }),

    // 子传父
    edit(id) {
      this.$emit("edit", id);
    },

    // 修改页码
    cPage(a) {
      this.changePage(a);
      this.requestList();
    },
    // 删除事件
    del(id) {
      this.$confirm("你确定要删除吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          requestNormsDelete({ id: id }).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg);
              this.requestTotal();
              this.requestList();
            } else {
              warningAlert(res.data.msg);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
};
</script>
<style scoped >
</style>


