<template>
  <div class="menu">
    <!-- 添加弹框 -->
    <v-alert :info="info" ref="add"></v-alert>

    <!-- 会员管理列表开始 -->
    <v-list @edit="edit"></v-list>
    <!-- 会员管理列表结束 -->
  </div>
</template>
<script>
import vList from "./components/vList";
import vAlert from "./components/vAlert";
export default {
  components: {
    vList,
    vAlert,
  },
  data() {
    return {
      info: {
        show: false,
        title: "会员添加",
        isAdd: true,
      },
    };
  },
  mounted() {},
  methods: {
    edit(id) {
      this.info.show = true;
      this.info.title = "会员编辑";
      this.info.isAdd = false;
      this.$refs.add.getDetail(id)
    },
  },
};
</script>
<style scoped >
.add {
  margin-bottom: 20px;
}
</style>


