<template>
  <div class>
    <!-- 会员管理添加弹框 -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="所属角色" prop="roleid">
          <el-select v-model="form.roleid">
            <el-option label="--请选择--" value disabled></el-option>
            <!-- 动态数据 -->
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :label="item.rolename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="用户名" prop="username">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input placeholder="请输入密码" v-model="form.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add(form)" v-if="info.isAdd">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { reg } from "../../../util/reg";
import {
  requestAdmiAdd,
  requestAdmiDetail,
  requestAdmiUpdate,
} from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      roleList: "role/list",
      list: "admi/list",
    }),
  },
  data() {
    return {
      form: {
        username: "",
        password: "",
        roleid: "",
        status: 1,
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        roleid: [
          { required: true, message: "请选择所属角色", trigger: "change" },
        ],
        password: [
          { required: true, message: "请请输入密码", trigger: "change" },
        ],
      },
    };
  },
  props: ["info"],
  mounted() {
    //如果之前menu的list没有请求，就发请求，请求你过了，就不发了
    if (this.roleList.length === 0) {
      this.requestRoleList();
    }
  },
  methods: {
    //   vuex获取数据
    ...mapActions({
      requestList: "admi/requestList",
      requestRoleList: "role/requestList",
      requestTotal: "admi/requestTotal",
    }),

    //重置内容
    empty() {
      this.form = {
        username: "",
        password: "",
        roleid: "",
        status: 1,
      };
    },

    // 取消
    cancel() {
      this.$refs.form.resetFields();
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 添加
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          requestAdmiAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg); //添加成功弹框
              this.info.show = false;
              this.cancel();
              this.empty();
              //   再次请求数据
              this.requestList();
              //重新获取总的数量
              this.requestTotal();
            } else {
              warningAlert(res.data.msg);
            }
          });
        } else {
          warningAlert("请将信息填写完整");
          return false;
        }
      });
    },

    // 获取某一条数据
    getDetail(id) {
      requestAdmiDetail({ uid: id }).then((res) => {
        this.form = res.data.list;
        this.form.password = "";
      });
    },

    // 修改
    update() {
      requestAdmiUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   再次请求数据
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped >
</style>


