<template>
  <div class>
    <!-- 活动管理添加弹框 -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="活动名称" prop="title">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="活动期限">
          <el-date-picker
            v-model="times"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="一级分类" prop="first_cateid">
          <el-select v-model="form.first_cateid" placeholder="请选择分类" @change="changeSortList()">
            <!-- 动态数据 -->
            <el-option
              v-for="item in sortList"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="二级分类" prop="second_cateid">
          <el-select v-model="form.second_cateid" placeholder="请选择分类" @change="changeGoodsname()">
            <!-- 动态数据 -->
            <el-option
              v-for="item in sortListChildren"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品" prop="goodsid">
          <el-select v-model="form.goodsid" placeholder="请选择商品">
            <!-- 动态数据 -->
            <el-option
              v-for="item in goodsname"
              :key="item.id"
              :label="item.goodsname"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="状态">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('form')">取 消</el-button>
        <el-button type="primary" @click="add('form')" v-if="info.isAdd">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template> 
<script>
import { filterTime } from "../../../filters/filterTime";
import { successAlert, warningAlert } from "../../../util/alert";
import { reg } from "../../../util/reg";
import { mapGetters, mapActions } from "vuex";
import {
  requestSeckAdd,
  requestSeckDetail,
  requestGoodsList,
  requestSeckUpdate,
} from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      list: "seck/list",
      goodsList: "goods/list",
      // 分类一级列表
      sortList: "sort/list",
    }),
  },
  data() {
    return {
      // 二级分类列表
      sortListChildren: [],
      goodsname: [],
      times: [],
      form: {
        title: "",
        begintime: "",
        endtime: "",
        first_cateid: "",
        second_cateid: "",
        goodsid: "",
        status: 1,
      },
      rules: {
        title: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
        first_cateid: [
          { required: true, message: "请选择一级分类", trigger: "change" },
        ],
        second_cateid: [
          { required: true, message: "请选择二级分类", trigger: "change" },
        ],
        goodsid: [{ required: true, message: "请选择商品", trigger: "change" }],
      },
    };
  },
  props: ["info"],
  mounted() {
    this.requestSortList();
  },
  methods: {
    //   vuex获取数据
    ...mapActions({
      requestSortList: "sort/requestList",
      requestList: "seck/requestList",
    }),
    // 获取二级列表
    changeSortList(bool) {
      let index = this.sortList.findIndex(
        (item) => item.id == this.form.first_cateid
      );
      this.sortListChildren = this.sortList[index].children;
      //传了true,second_cateid就不置空；没传就置空
      if (!bool) {
        this.form.second_cateid = "";
      }
    },
    // 获取商品列表
    changeGoodsname(bool) {
      requestGoodsList({
        fid: this.form.first_cateid,
        sid: this.form.second_cateid,
      }).then((res) => {
        this.goodsname = res.data.list;
      });
      if (!bool) {
        this.form.goodsid = "";
      }
    },

    //重置内容
    empty() {
      // 二级分类列表
      (this.sortListChildren = []),
        // 三级商品
        (this.goodsname = []),
        (this.form = {
          title: "",
          begintime: "",
          endtime: "",
          first_cateid: "",
          second_cateid: "",
          goodsid: "",
          status: 1,
        });
      this.times = [];
    },
    // 取消
    cancel(form) {
      this.$refs.form.resetFields();
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 添加
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          // 将时间转换成时间戳
          this.form.begintime = this.times[0].getTime();
          this.form.endtime = this.times[1].getTime();
          requestSeckAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert("添加成功"); //添加成功弹框
              this.info.show = false;
              this.empty();
              //   再次请求数据
              this.requestList();
              
              this.cancel();
            } else {
              warningAlert("添加失败");
            }
          });
        } else {
          warningAlert("请将信息填写完整");
          return false;
        }
      });
    },

    // 获取某一条数据
    getDetail(id) {
      requestSeckDetail({ id: id }).then((res) => {
        this.form = res.data.list;
        var a = new Date(Number(res.data.list.begintime));
        var b = new Date(Number(res.data.list.endtime));
        this.times = [a, b];
        this.form.id = id;
        this.changeSortList(true);
        this.changeGoodsname(true);
      });
    },
    // 修改
    update() {
      this.form.begintime = Date.parse(new Date(this.times[0]));
      this.form.endtime = Date.parse(new Date(this.times[1]));
      requestSeckUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   再次请求数据
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped >
</style>


