<template>
  <div class="sort">
    <!-- 会员管理修改弹框 -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="手机号" prop="phone">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="昵称" prop="nickname">
          <el-input v-model="form.nickname"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input placeholder="请输入密码" v-model="form.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="update(form)">修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import {
  requestMemberUpdate,
  requestMemberDetail,
} from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      list: "member/list",
    }),
  },
  data() {
    return {
      form: {
        uid: 0,
        status: 1,
        nickname: "",
        phone: "",
        password: "",
      },
      rules: {
        phone: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 11, max: 11, message: "长度11位", trigger: "blur" },
        ],
        password: [
          { required: true, message: "请请输入密码", trigger: "change" },
        ],
        nickname: [
          { required: true, message: "请输入用户名", trigger: "change" },
        ],
      },
    };
  },
  props: ["info"],
  mounted() {},
  methods: {
    //   vuex获取数据
    ...mapActions({
      requestList: "member/requestList",
    }),
    //重置内容
    empty() {
      this.form = {
        uid: 0,
        status: 1,
        nickname: "",
        phone: "",
        password: "",
      };
    },
    // 取消
    cancel() {
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },
    // 获取某一条数据
    getDetail(id) {
      requestMemberDetail({ uid: id }).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.form.password = "";
      });
    },
    // 修改
    update(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          requestMemberUpdate(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg);
              this.empty();
              this.cancel();
              //   再次请求数据
              this.requestList();
              // 清除验证结果
              this.$refs.form.resetFields();
            } else {
              warningAlert(res.data.msg);
            }
          });
        } else {
          warningAlert("请将信息填写完整");
          return false;
        }
      });
    },
  },
};
</script>
<style scoped lang='stylus'>
.sort >>> .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.sort >>> .el-upload:hover {
  border: 1px dashed #409EFF;
}

.sort >>> .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.sort >>> .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>


