<template>
  <div class="norms">
    <!-- 规格管理添加弹框 -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="规格名称" prop="specsname">
          <el-input v-model="form.specsname"></el-input>
        </el-form-item>
        <el-form-item label="规格属性" v-for="(item,index) in attrArr" :key="index">
          <el-input v-model="item.value"></el-input>
          <el-button type="primary" @click="addAttr" v-if="index==0">新增规格属性</el-button>
          <el-button type="danger" @click="delAttr(index)" v-else>删除</el-button>
        </el-form-item>
        <el-form-item label="状态">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add" v-if="info.isAdd">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { reg } from "../../../util/reg";
import {
  requestNormsAdd,
  requestNormsDetail,
  requestNormsUpdate,
} from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      list: "norms/list",
    }),
  },
  data() {
    return {
      form: {
        specsname: "",
        status: 1,
      },
      rules: {
        specsname: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
      },
      attrArr: [
        {
          value: "",
        },
      ],
    };
  },
  props: ["info"],
  mounted() {},
  methods: {
    //   vuex获取数据
    ...mapActions({
      requestList: "norms/requestList",
      requestTotal: "norms/requestTotal",
    }),

    //重置内容
    empty() {
      this.form = {
        specsname: "",
        status: 1,
      };
      this.attrArr = [
        {
          value: "",
        },
      ];
    },

    // 取消
    cancel() {
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 添加规格属性
    addAttr() {
      this.attrArr.push({ value: "" });
    },
    // 删除规格属性
    delAttr(index) {
      this.attrArr.splice(index, 1);
    },

    // 添加
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.attrArr.some((item) => item.value == "")) {
            warningAlert("属性规格均不能为空");
            return;
          }
          this.form.attrs = JSON.stringify(
            this.attrArr.map((item) => item.value)
          );
          requestNormsAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg); //添加成功弹框
              this.info.show = false;
              this.cancel();
              this.empty();
              //   再次请求数据
              this.requestList();
              //重新获取总的数量
              this.requestTotal();
            } else {
              warningAlert(res.data.msg);
            }
          });
        } else {
          warningAlert("请按要求将信息填写完整");
          return false;
        }
      });
    },

    // 获取某一条数据
    getDetail(id) {
      requestNormsDetail({ id: id }).then((res) => {
        this.form = res.data.list[0];
        this.attrArr = JSON.parse(res.data.list[0].attrs).map((item) => ({
          value: item,
        }));
      });
    },

    // 修改
    update() {
      if (this.attrArr.some((item) => item.value == "")) {
        warningAlert("属性规格均不能为空");
        return;
      }
      this.form.attrs = JSON.stringify(this.attrArr.map((item) => item.value));
      requestNormsUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   再次请求数据
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped long='stylus'>
.norms >>> .el-form-item__content {
  display: flex;
}
.norms >>> .el-button {
  width: auto;
}
.norms >>> .el-input {
  flex: 1;
}
</style>


