import Vue from 'vue'
import Router from 'vue-router'
import login from '../pages/login/login.vue'
import index from '../pages/index/index.vue'
import home from '../pages/home/home.vue'
import role from "../pages/role/role"
import admi from "../pages/admi/admi"
import menu from "../pages/menu/menu"
import sort from "../pages/sort/sort.vue"
import norms from "../pages/norms/norms.vue"
import goods from "../pages/goods/goods.vue"
import member from "../pages/member/member.vue"
import banner from "../pages/banner/banner.vue"
import seck from "../pages/seck/seck.vue"
Vue.use(Router)


// 路由独享
import store from "../store/index"
function havePower(url) {
  return store.state.user.menus_url.some(i => i == url)
}

let router = new Router({
  routes: [
    {
      path: "/login",
      component: login
    },
    {
      path: "/",
      component: index,
      children: [
        {
          path: "home",
          component: home,
          name: '首页'
        },
        {
          path: "role",
          component: role,
          name: '角色管理',
          beforeEnter(to, from, next) {
            havePower('/role') ? next() : next("/home")
          }
        },
        {
          path: "admi",
          component: admi,
          name: '管理员管理',
          beforeEnter(to, from, next) {
            havePower('/admi') ? next() : next("/home")
          }
        },
        {
          path: "menu",
          component: menu,
          name: '菜单管理',
          beforeEnter(to, from, next) {
            havePower('/menu') ? next() : next("/home")
          }
        },
        {
          path: "sort",
          component: sort,
          name: '商品分类',
          beforeEnter(to, from, next) {
            havePower('/sort') ? next() : next("/home")
          }
        },
        {
          path: "norms",
          component: norms,
          name: '商品规格',
          beforeEnter(to, from, next) {
            havePower('/norms') ? next() : next("/home")
          }
        },
        {
          path: "goods",
          component: goods,
          name: '商品管理',
          beforeEnter(to, from, next) {
            havePower('/goods') ? next() : next("/home")
          }
        },
        {
          path: "member",
          component: member,
          name: '会员管理',
          beforeEnter(to, from, next) {
            havePower('/member') ? next() : next("/home")
          }
        },
        {
          path: "banner",
          component: banner,
          name: '轮播图管理',
          beforeEnter(to, from, next) {
            havePower('/banner') ? next() : next("/home")
          }
        },
        {
          path: "seck",
          component: seck,
          name: '秒杀活动',
          beforeEnter(to, from, next) {
            havePower('/seck') ? next() : next("/home")
          }
        },
        {
          path: "",
          redirect: "home"
        }
      ]
    }
  ]
})


// 登陆拦截
router.beforeEach((to, from, next) => {
  // 如果前往登录页面，next()
  if (to.path === '/login') {
    next();
    return;
  }

  // 如果不是去的登录，先判断 store.user.user是true则next(),否则next('/login)
  if (store.state.user) {
    next();
    return;
  }
  next('/login');
})


export default router



