<template>
  <div class>
    <!-- 角色管理添加弹框 -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="角色名称" prop="rolename">
          <el-input v-model="form.rolename"></el-input>
        </el-form-item>
        <el-tree
          :data="Menulist"
          show-checkbox
          node-key="id"
          :default-checked-keys="defaultKey"
          :props="defaultProps"
          ref="tree"
          prop="menus"
        ></el-tree>
        <el-form-item label="状态">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add(form)" v-if="info.isAdd">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { reg } from "../../../util/reg";
import {
  requestRoleAdd,
  requestRoleDetail,
  requestRoleUpdate,
} from "../../../util/request";
export default {
  computed: {
    //   vuex获取数据
    ...mapGetters({
      list: "role/list",
      Menulist: "menu/list",
    }),
  },
  data() {
    return {
      form: {
        status: 1,
        rolename: "",
        menus: "",
      },
      rules: {
        rolename: [
          { required: true, message: "请输入角色名称", trigger: "blur" },
          { min: 2, max: 6, message: "长度在 2 到 6 个字符", trigger: "blur" },
        ],
      },
      //树形结构的数据
      defaultProps: {
        children: "children",
        label: "title",
      },
      defaultKey: [],
    };
  },
  props: ["info"],
  mounted() {
    //如果之前menu的list没有请求，就发请求，请求你过了，就不发了
    if (this.Menulist.length === 0) {
      this.requestMenuList();
    }
  },

  methods: {
    //   vuex获取数据
    ...mapActions({
      requestList: "role/requestList",
      requestMenuList: "menu/requestList",
    }),

    //重置内容
    empty() {
      this.form = {
        status: 1,
        rolename: "",
        menus: "",
      };
      this.$refs.tree.setCheckedKeys([]);
    },
    // 取消
    cancel() {
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 添加
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys());
          if (this.form.menus.length - 2 == 0) {
            warningAlert("角色权限至少选择一项");
            return;
          }
          // 如果没输入信息则提示
          requestRoleAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg); //添加成功弹框
              this.info.show = false;
              this.cancel();
              this.empty();
              //   再次请求数据
              this.requestList();
              // 清除验证结果
              this.$refs[form].resetFields();
            } else {
              warningAlert(res.data.msg);
            }
          });
        } else {
          warningAlert("请将信息填写完整");
          return false;
        }
      });
    },

    // 获取某一条数据
    getDetail(id) {
      requestRoleDetail({ id: id }).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.defaultKey = JSON.parse(res.data.list.menus);
      });
    },

    // 修改
    update() {
      this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys());
      requestRoleUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   再次请求数据
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped >
</style>


