<template>
  <div class="inner">
    <div class="login">
      <p>登录</p>
      <div class="uName">
        <el-input placeholder="请输入内容" v-model="form.username" clearable></el-input>
      </div>
      <div class="uPass">
        <el-input placeholder="请输入密码" v-model="form.password" show-password></el-input>
      </div>
      <div class="uSub">
        <input type="submit" value="登录 " @click="login" />
      </div>
    </div>
  </div>
</template>

<script>
import { successAlert, warningAlert } from "../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { requestAdmiLogin } from "../../util/request";
export default {
  data() {
    return {
      form: {
        username: "",
        password: "",
      },
    };
  },
  props: [],
  mounted() {},
  computed: {
    ...mapGetters({
      userList: "user",
    }),
  },
  methods: {
    ...mapActions({
      changeUser: "changeUser",
    }),
    login() {
      requestAdmiLogin(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.changeUser(res.data.list);
          console.log(this.userList);
          this.$router.push("/");
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>

<style scoped>
.inner {
  width: 100vw;
  height: 100vh;
  position: relative;
  background: #333d5e;
}
.login {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  height: 300px;
  border-radius: 20px;
  border: 1px solid;
  background: #fff;
}
p {
  margin-top: 13px;
  font: 23px/68px "微软雅黑";
  text-align: center;
}
.uName,
.uPass,
.uSub {
  width: 320px;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 22px;
}
input {
  width: 100%;
  font: 12px/38px "微软雅黑";
  padding-left: 15px;
  box-sizing: border-box;
  border: 1px solid #c0c4cc;
  border-radius: 3px;
  color: #c0c4cc;
}
.uSub input {
  background: #409eff;
  border: 1px solid #409eff;
  color: #fff;
}
</style>


