<template>
  <div class="goods">
    <!-- 会员管理添加弹框 -->
    <el-dialog :title="info.title" :visible.sync="info.show" @opened="establishDesc">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="一级分类" prop="first_cateid">
          <el-select v-model="form.first_cateid" placeholder="请选择分类" @change="changeSortList()">
            <!-- 动态数据 -->
            <el-option
              v-for="item in sortList"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="二级分类" prop="second_cateid">
          <el-select v-model="form.second_cateid" placeholder="请选择分类">
            <!-- 动态数据 -->
            <el-option
              v-for="item in sortListChildren"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品名称" prop="goodsname">
          <el-input v-model="form.goodsname"></el-input>
        </el-form-item>
        <el-form-item label="商品价格" prop="price">
          <el-input v-model="form.price"></el-input>
        </el-form-item>
        <el-form-item label="市场价格" prop="market_price">
          <el-input v-model="form.market_price"></el-input>
        </el-form-item>
        <el-form-item label="图片">
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-change="changeImg"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="商品规格" prop="specsid">
          <el-select v-model="form.specsid" placeholder="请选择" @change="changeNormsList()">
            <!-- 动态数据 -->
            <el-option
              v-for="item in normsList"
              :key="item.id"
              :label="item.specsname"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品属性" prop="specsattr">
          <el-select v-model="form.specsattr" placeholder="请选择">
            <!-- 动态数据 -->
            <el-option v-for="item in normsListAttrs" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否新品">
          <el-radio v-model="form.isnew" :label="1">是</el-radio>
          <el-radio v-model="form.isnew" :label="2">否</el-radio>
        </el-form-item>
        <el-form-item label="是否热卖">
          <el-radio v-model="form.ishot" :label="1">是</el-radio>
          <el-radio v-model="form.ishot" :label="2">否</el-radio>
        </el-form-item>
        <el-form-item label="状态">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
        <el-form-item label="商品描述">
          <div id="desc"></div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add(form)" v-if="info.isAdd">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { reg } from "../../../util/reg";
import {
  requestGoodsAdd,
  requestGoodsDetail,
  requestGoodsUpdate,
} from "../../../util/request";
import E from "wangeditor";
export default {
  computed: {
    ...mapGetters({
      // 规格列表
      normsList: "norms/list",
      // 分类一级列表
      sortList: "sort/list",
    }),
  },
  data() {
    return {
      // 二级分类列表
      sortListChildren: [],
      // 二级规格列表
      normsListAttrs: [],
      imageUrl: "",
      form: {
        first_cateid: "",
        second_cateid: "",
        goodsname: "",
        price: null,
        market_price: null,
        img: null,
        description: "",
        specsid: "",
        specsattr: "",
        isnew: 1,
        ishot: 1,
        status: 1,
      },
      rules: {
        goodsname: [
          { required: true, message: "请输入商品名称", trigger: "blur" },
        ],
        price: [{ required: true, message: "请输入商品价格", trigger: "blur" }],
        market_price: [
          { required: true, message: "请输入市场价格", trigger: "blur" },
        ],
        first_cateid: [
          { required: true, message: "请选择一级分类", trigger: "change" },
        ],
        second_cateid: [
          { required: true, message: "请选择二级分类", trigger: "change" },
        ],
        specsid: [
          { required: true, message: "请选择商品规格", trigger: "change" },
        ],
        specsattr: [
          { required: true, message: "请选择商品属性", trigger: "change" },
        ],
      },
    };
  },
  props: ["info"],
  mounted() {
    this.requestSortList();
    //获取所有的规格数据
    this.requestNormsList(true);
  },
  methods: {
    //   vuex获取数据
    ...mapActions({
      requestSortList: "sort/requestList",
      requestNormsList: "norms/requestList",
      requestList: "goods/requestList",
      requestTotal: "goods/requestTotal",
    }),
    // 获取二级列表
    changeSortList(bool) {
      let index = this.sortList.findIndex(
        (item) => item.id == this.form.first_cateid
      );
      this.sortListChildren = this.sortList[index].children;
      //传了true,second_cateid就不置空；没传就置空
      if (!bool) {
        this.form.second_cateid = "";
      }
    },
    //商品规格发生了改变
    changeNormsList(bool) {
      let index = this.normsList.findIndex(
        (item) => item.id == this.form.specsid
      );
      this.normsListAttrs = this.normsList[index].attrs;
      if (!bool) {
        this.form.specsattr = "";
      }
    },
    // 创建文本框
    establishDesc() {
      this.editor = new E("#desc");
      this.editor.create();
      this.editor.txt.html(this.form.description);
    },

    // changeImg
    changeImg(e) {
      // 上传文件不能超过2M
      if (e.size > 2 * 1024 * 1024) {
        warningAlert("上传图片必须不能超过2M");
        return;
      }
      // 上传的文件
      var file = e.raw;
      this.imageUrl = URL.createObjectURL(file);
      this.form.img = file;
    },

    //重置内容
    empty() {
      // 二级分类列表
      (this.sortListChildren = []),
        // 二级规格列表
        (this.normsListAttrs = []),
        // 上传图片
        (this.imageUrl = ""),
        (this.form = {
          first_cateid: "",
          second_cateid: "",
          goodsname: "",
          price: 0,
          market_price: 0,
          img: null,
          description: "",
          specsid: "",
          specsattr: "",
          isnew: 1,
          ishot: 1,
          status: 1,
        });
      this.editor.txt.html("");
    },

    // 取消
    cancel() {
      this.$refs.form.resetFields();
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 添加
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          // 判断是否上传图片
          if (this.form.img === null) {
            warningAlert("请上传图片");
            return;
          }
          // 判断是否有商品描述
          this.form.description = this.editor.txt.html();
          if (this.form.description == "<p><br></p>") {
            warningAlert("请添加商品描述");
            return;
          }
          this.form.specsattr = JSON.stringify(this.form.specsattr);
          requestGoodsAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg); //添加成功弹框
              this.info.show = false;
              this.cancel();
              this.empty();
              //   再次请求数据
              this.requestList();
              //重新获取总的数量
              this.requestTotal();
            } else {
              warningAlert(res.data.msg);
            }
          });
        } else {
          warningAlert("请将信息填写完整");
          return false;
        }
      });
    },

    // 获取某一条数据
    getDetail(id) {
      requestGoodsDetail({ id: id }).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.imageUrl = this.$imgPre + this.form.img;
        //根据一级分类计算出二级分类的数组
        this.changeSortList(true);
        //根据商品规格计算出商品属性
        this.changeNormsList(true);
        this.form.specsattr = JSON.parse(this.form.specsattr);
      });
    },

    // 修改
    update() {
      this.form.description = this.editor.txt.html();
      this.form.specsattr = JSON.stringify(this.form.specsattr);
      requestGoodsUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   再次请求数据
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped long='stylus'>
.goods >>> .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.goods >>> .el-upload:hover {
  border: 1px dashed #409eff;
}

.goods >>> .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.goods >>> .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>


