<template>
  <div class="index">
    <el-container>
      <el-aside width="200px">
        <!-- 导航开始 -->
        <el-menu
          class="el-menu-vertical-demo"
          background-color="#20222a"
          text-color="#fff"
          active-text-color="#ffd04b"
          router
          :unique-opened="true"
        >
          <el-menu-item index="/home">
            <i class="el-icon-setting"></i>
            <span slot="title">首页</span>
          </el-menu-item>
          <!-- <el-submenu :index="index+''" v-for="(item,index) in userList.menus">
            <template slot="title">
              <i v-if="item.icon" :class="item.icon"></i>
              <span>{{item.title}}</span>
            </template>
            <template v-if="item.children">
              <el-menu-item :index="i.url" v-for="i in item.children" :key="i.id">{{i.title}}</el-menu-item>
            </template>
            <template v-else>
              <el-menu-item :index="item.url" :key="item.id">{{item.title}}</el-menu-item>
            </template>
          </el-submenu>-->

          <template v-for="(item) in userList.menus">
            <el-submenu :index="item.id+''" :key="item.id" v-if="item.children">
              <template slot="title">
                <i :class="item.icon"></i>
                <span>{{item.title}}</span>
              </template>
              <el-menu-item :index="i.url" v-for="i in item.children" :key="i.title">{{i.title}}</el-menu-item>
            </el-submenu>
            <el-menu-item :index="item.url" :key="item.id" v-if="!item.children">
              <span slot="title">{{item.title}}</span>
            </el-menu-item>
          </template>
        </el-menu>
        <!-- 导航结束 -->
      </el-aside>
      <el-container>
        <el-header>
          <div class="header fr">
            <span>{{userList.username}}</span>
            <el-button type="primary" @click="quit">退 出</el-button>
          </div>
        </el-header>
        <el-main>
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>{{$route.name}}</el-breadcrumb-item>
          </el-breadcrumb>
          <router-view class="view"></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import { successAlert, warningAlert } from "../../util/alert";
import { mapGetters, mapActions } from "vuex";
export default {
  computed: {
    ...mapGetters({
      userList: "user",
    }),
    // //用来判断是否有目录
    // hasChildren() {
    //   return this.userList.menus[0].children ? true : false;
    // },
  },
  data() {
    return {};
  },
  mounted() {},
  methods: {
    ...mapActions({
      changeUser: "changeUser",
    }),
    // 退出登录
    quit() {
      this.$router.push("/login");
      this.changeUser(null);
    },
  },
};
</script>

<style scoped >
.el-aside {
  height: 100vh;
  background: #20222a;
  color: #fff;
}
.el-header {
  background: #b3c0d1;
  overflow: hidden;
  line-height: 60px;
  color: #fff;
}
.view {
  padding-top: 20px;
}
</style>


