<template>
  <div class="menu">
    <el-button type="primary" @click="add" class="add">添 加</el-button>
    <!-- 添加弹框 -->
    <v-alert :info="info" ref="add"></v-alert>

    <!-- 管理员管理列表开始 -->
    <v-list @edit="edit"></v-list>
    <!-- 管理员管理列表结束 -->
  </div>
</template>
<script>
import vList from "./components/vList";
import vAlert from "./components/vAlert";
export default {
  components: {
    vList,
    vAlert,
  },
  data() {
    return {
      info: {
        show: false,
        title: "商品添加",
        isAdd: true,
      },
    };
  },
  mounted() {},
  methods: {
    // 点击添加
    add(id) {
      (this.info.show = true),
        (this.info.tatle = "商品添加"),
        (this.info.isAdd = true);
    },
    //   点击修改(子传父)
    edit(id) {
      this.info.show = true;
      this.info.title = "商品编辑";
      this.info.isAdd = false;
      this.$refs.add.getDetail(id)
    },
  },
};
</script>
<style scoped >
.add {
  margin-bottom: 20px;
}
</style> 


