<template>
  <div class="sort">
    <!-- 商品分类添加弹框 -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="上级分类" prop="pid">
          <el-select v-model="form.pid">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option label="顶级分类" :value="0"></el-option>
            <!-- 动态数据 -->
            <el-option v-for="item in list" :key="item.id" :label="item.catename" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分类名称" prop="catename">
          <el-input v-model="form.catename"></el-input>
        </el-form-item>
        <el-form-item label="图片" v-if="form.pid!==0">
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-change="changeImg"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="状态">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="add('form')" v-if="info.isAdd">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { reg } from "../../../util/reg";
import {
  requestSortAdd,
  requestSortDetail,
  requestSortUpdate,
} from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      list: "sort/list",
    }),
  },
  data() {
    return {
      imageUrl: "",
      form: {
        pid: 0,
        status: 1,
        catename: "",
        img: null,
      },
      rules: {
        catename: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
        pid: [{ required: true, message: "请选择分类", trigger: "change" }],
      },
    };
  },
  props: ["info"],
  mounted() {},
  methods: {
    //   vuex获取数据
    ...mapActions({
      requestList: "sort/requestList",
    }),

    // changeImg
    changeImg(e) {
      // 上传文件不能超过2M
      if (e.size > 2 * 1024 * 1024) {
        warningAlert("上传图片必须不能超过2M");
        return;
      }
      // 上传文件必须是图片
      var extname = e.name.slice(e.name.lastIndexOf("."));
      var extArr = [".png", ".jpg", ".gif", ".jpeg"];
      if (!extArr.some((i) => i === extname)) {
        warningAlert("上传文件必须是图片");
        return;
      }
      // 上传的文件
      var file = e.raw;
      this.imageUrl = URL.createObjectURL(file);
      this.form.img = file;
    },

    //重置内容
    empty() {
      (this.form = {
        pid: 0,
        status: 1,
        catename: "",
        img: null,
      }),
        (this.imageUrl = "");
    },
    // 取消
    cancel() {
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 添加
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          // 判断是否上传图片
          if (this.form.img === null && this.form.pid !== 0) {
            warningAlert("请上传图片");
            return;
          }
          requestSortAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert("添加成功"); //添加成功弹框
              this.info.show = false;
              this.cancel();
              this.empty();
              //   再次请求数据
              this.requestList();
            }
          });
        } else {
          warningAlert("请将信息填写完整");
          return false;
        }
      });
    },

    // 获取某一条数据
    getDetail(id) {
      requestSortDetail({ id: id }).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.imageUrl = this.$imgPre + res.data.list.img;
      });
    },

    // 修改
    update() {
      requestSortUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   再次请求数据
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped lang='stylus'>
.sort >>> .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.sort >>> .el-upload:hover {
  border: 1px dashed #409EFF;
}

.sort >>> .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.sort >>> .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>


